body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-container {
  height: 100vh;
  display: flex;
  flex-flow: column;
  align-items: center;
}

.home-container h2 {
  /* padding-top: 3rem; */
  text-align: center;
  font-weight: 200;
  font-size: 4rem;
}

.home-card-container {
  width: 60%;
  display: flex;
  justify-content: space-evenly;
  flex-flow: wrap;
}

.home-card {
  border: 1px solid rgb(239, 239, 239);
  border-radius: 5px;
  width: 305px;
  height: 400px;
  background: rgb(255, 255, 255);
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  display: flex;
  justify-content: space-around;
  flex-flow: column;
  align-items: center;
  margin-bottom: 1.5rem;
}

.home-card:hover {
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  border: 1px solid #89aee1;
}

.home-card-img {
  /* width: 130px; */
  height: 130px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

.home-card-button {
  background: transparent;
  padding: 10px 30px;
  border: 1px solid rgb(200, 200, 200);
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
  width: 122px;
}

.home-card-button:hover {
  background: #89aee1;
  border: 1px solid #89aee1;
  cursor: pointer;
}
.home-card-button-bottom {
  background: transparent;
  padding: 10px 30px;
  border: 1px solid rgb(200, 200, 200);
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
}

.home-card-button-bottom:hover {
  background: #89aee1;
  cursor: pointer;
}

.home-card-span {
  padding: 0 25px;
  text-align: center;
}


@media only screen and (max-width: 520px) {
  .home-card-container {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    flex-flow: wrap;
    margin-top: 25px;
    margin-bottom: 25px;
  }

  .home-card {
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    width: 85%;
  }
}
.nav-container {
  max-width: 100%;
  background: white;
}

.nav-content-container {
  background: white;
  height: 30px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgb(231, 231, 231);
  padding: 5px 0;
  position: fixed;
  top: 0;
}

.nav-social-icon-container {
  padding-right: 15px;
}

.nav-name {
  padding-left: 15px;
  text-transform: uppercase;
}

.nav-home-image {
  width: 35px;
  padding-top: 5px;
  padding-right: 18px;
}

.nav-social-image {
  width: 35px;
  padding-top: 5px;
}

.nav-icon-padding {
  padding-right: 8px;
}

