.cards-wrapper {
  border: 1px solid rgb(239, 239, 239);
  border-radius: 3px;
  width: 300px;
  height: 455px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  margin-bottom: 20px;
  margin-right: 10px;
  margin-left: 10px;
}

.cards-wrapper:hover {
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  border: 1px solid #89aee1;
}

.cards-img-wrapper {
  width: 100%;
  height: 120px;
  overflow: hidden;
  border-bottom: 1px solid rgb(239, 239, 239);
}

.cards-img {
  /* object-fit: cover; */
  width: 100%;
  height: auto;
}

.cards-title {
  /* color: rgb(103, 197, 209); */
  color: #89aee1;
  font-style: italic;
  text-transform: uppercase;
}

.cards-font-size {
  font-size: 0.8rem;
}

.cards-content-wrapper {
  padding: 0 6px;
}

.cards-live-link {
  margin-right: 6px;
  text-decoration: none;
  color: #89aee1;
}

.cards-live-link:hover {
  color: #7cf4d3
}

.cards-gh-link {
  margin-left: 6px;
  text-decoration: none;
  color: #89aee1;
}

.cards-gh-link:hover {
  color: #7cf4d3
}

@media only screen and (max-width: 520px) {
  .cards-wrapper {
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    border: 1px solid #e6e6e6;
    width: 100%;
    margin-bottom: 26px;
    margin-left: 0;
    margin-right: 0;
  }
}
